import React from 'react';
import CheckboxTree from 'react-checkbox-tree';

const nodes = [
    {
        value: 'Documents',
        label: 'Documents',
        children: [
            {
                value: 'Employee Evaluations.zip',
                label: 'Employee Evaluations.zip',
                icon: <i className="far fa-file-archive" />,
            },
            {
                value: 'Expense Report.pdf',
                label: 'Expense Report.pdf',
                icon: <i className="far fa-file-pdf" />,
            },
            {
                value: 'notes.txt',
                label: 'notes.txt',
                icon: <i className="far fa-file-alt" />,
            },
        ],
    },
    {
        value: 'Photos',
        label: 'Photos',
        children: [
            {
                value: 'nyan-cat.gif',
                label: 'nyan-cat.gif',
                icon: <i className="fa fa-file-image-o" />,
            },
            {
                value: 'SpaceX Falcon9 liftoff.jpg',
                label: 'SpaceX Falcon9 liftoff.jpg',
                icon: <i className="fa fa-file-image-o" />,
            },
        ],
    },
];

class Kazakhmys extends React.Component {
    state = {
        checked: [],
        expanded: [
            'Documents',
        ],
    };

    constructor(props) {
        super(props);

        this.onCheck = this.onCheck.bind(this);
        this.onExpand = this.onExpand.bind(this);
    }

    onCheck(checked) {
        this.setState({ checked });
        const names = checked.value;
        console.log(names);
    }

    onExpand(expanded) {

    }

    render() {
        const { checked, expanded } = this.state;
        const notClickedText = '(none)';
        return (

            <div id = "sidebar" className="filter-group">
                <CheckboxTree
                    checked={checked}
                    expanded={expanded}
                    iconsClass="fa5"
                    nodes={nodes}
                    onCheck={this.onCheck}
                    onExpand={this.onExpand}
                />
                <strong>Clicked Node</strong>: {checked.value || notClickedText}
            </div>

        );
    }
}

export default Kazakhmys;